.contacts {
    
}

.contacts__content {
    padding: 25px 20px;
}

.contacts__title {
    font-family: 'Oswald', sans-serif;
    font-size: 16px;
    line-height: 1.25;
    font-style: normal;
    font-weight: 400;
    text-transform: uppercase;
    text-align: left;

    color: @color-gray-900;
}

.contacts__adress {
    font-family: 'Arial', sans-serif;
    font-size: 14px;
    line-height: 1.4;
    font-style: normal;
    font-weight: 400;
    text-align: left;
    
    color: @color-gray-700;
}